import React , {FunctionComponent} from 'react';
import Button from '@material-ui/core/Button';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import { Divider } from '@material-ui/core';


interface MenuProp{
    menuName : string,
    isEnable : boolean,
    items : any[],
    onClickMenu : (id : string) => void,
    onClick ?: () => void
}

export const MenuButton: FunctionComponent<MenuProp> = ({onClick,isEnable , items , menuName , onClickMenu}) => {

  const [open, setOpen] = React.useState(false);
  const anchorRef = React.useRef<HTMLButtonElement>(null);

  const handleToggle = () => {
    if(items.length > 0){
      setOpen((prevOpen) => !prevOpen);
    }else{
      if(onClick === undefined) return
      onClick()
    }
  };


  const handleClose = (event: React.MouseEvent<EventTarget> , id : string) => {
    if (anchorRef.current && anchorRef.current.contains(event.target as HTMLElement)) {
      return;
    }

    setOpen(false);
    onClickMenu(id)
  };

    return  (
        <React.Fragment>
        <span
          style={{color : "white" , textTransform : "capitalize" , marginRight : 25 , cursor: "pointer"}}
          ref={anchorRef}
          aria-controls={open ? 'menu-list-grow' : undefined}
          aria-haspopup="true"
          onClick={handleToggle}
        >
          {menuName}
        </span>
        <Popper style={{zIndex:100 }} open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
          {({ TransitionProps, placement }) => (
            <Grow
              {...TransitionProps}
              style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
            >
              <Paper>
                <ClickAwayListener onClickAway={(e)=>handleClose(e,"")}>
                  <MenuList autoFocusItem={open} id="menu-list-grow">
                  {items.map(item=>{
                    return (
                      <div>

                        {(item.isLine) ? (<Divider></Divider>) : ""}

                        <MenuItem disabled={!isEnable} onClick={(e)=>handleClose(e,item.id)} key={menuName + item.id}>{item.text}</MenuItem>
                         

                      </div>
                        
                    )
                })}
                
                  </MenuList>
                </ClickAwayListener>
              </Paper>
            </Grow>
          )}
        </Popper>
      </React.Fragment>
    )
}